.twitter-container {
  text-align: center;
  padding: 100px 0 50px 0;
  font-family: 'Lato', sans-serif;
  letter-spacing: 1.5px;

  p {
    color: #cad6e6;
    font-size: 30px;
    letter-spacing: 3px;
    font-weight: 300;
  }

  .tweet-container {
    padding-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;

    .twitter-element {
      box-shadow: 0 0px 20px -2px rgba(255 205 238 / 70%);
      border-radius: 7px;
      width: 300px;
      margin: 0 5px;
    }
  }
}

@media only screen and (max-width: 800px) {
  .twitter-container {
    .tweet-container {
      display: flex;
      flex-direction: column;
      align-items: center;

      .twitter-element {
        margin: 20px 0;
      }
    }
  }
}
